.ud-rating-block {
	text-align: center;
	margin: 2em auto;
	max-width: 400px;

	&__question {
		font-size: 1.25rem;
		font-weight: 500;
		margin-bottom: 1em;
	}

	&__stars {
		display: flex;
		justify-content: center;
		gap: 0.5rem;
		cursor: pointer;

		svg {
			width: 36px;
			height: 36px;
			transition: transform 0.2s ease, fill 0.2s ease;

			&:hover {
				transform: scale(1.15);
			}
		}
	}

	&__thankyou,
	&__google {
		margin-top: 1.5em;
		animation: fadeIn 0.4s ease forwards;
	}

	&__google-link {
		display: inline-block;
		background: #fabb05;
		color: #000;
		font-weight: 600;
		text-decoration: none;
		padding: 0.5em 1em;
		border-radius: 6px;
		transition: background 0.2s;

		&:hover {
			background: #ffd84d;
		}
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translateY(5px);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}
